body.is-open{
  overflow:hidden;
}
.popup__login {
  height: 100%;
  .container {
    text-align: center;
    padding: 40px 0px  0px !important;
    @media(--pad){
    &:after {
      content: "";
      display: inline-block;
      vertical-align: middle;
      height: 100%;
    }
  }
    .content {
      font-size: 14px;
      width: 100%;
      max-width: 450px;
      position: relative;
      display: inline-block;
      vertical-align: middle;
      background: #fff;
      .close-button {
        right: 0px;
        top: -40px;
        @media(--pad){
          right: -40px;
        }
      }
    }
    .login-div {
        .btns {
          margin-top: 20px;
          a {
            margin: 0 10px;
            color: #626262;
            text-decoration: underline;
            font-size: 16px;
          }
        }
      .login-data {
        width: 260px;
        margin: 10px auto;
        input:not([type="checkbox"]) {
          width: 100%;
          margin-bottom: 15px;
        }
        input[type="checkbox"] {
          margin-right: 10px;
        }
        label.email span {
          margin-bottom: 5px;
        }
        label.remeber {
          >input {
            display: inline-block;
            vertical-align: middle;
            margin-right: 5px;
          }
          >span {
            display: inline-block;
            vertical-align: middle;
          }
        }
      }
    }
    .main-div {
      &.login{
        .btns{
          margin: 100px 0;
        }
      }
      >div form {
        display: block;
        height: 420px;
        overflow-y: auto;
        padding: 30px 20px 0px;
        margin-bottom: 30px;
      }
      .signup-div,
      .getpass-div,
      .success-div {
        display: none;
      }
      input:not([type="checkbox"]) {
        background: #e8e8e8;
        height: 40px;
        border: 0;
        text-align: center;
        letter-spacing: 1px;
      }
      button {
        background: transparent;
        line-height: 32px;
        border: 1px solid #999;
        width: 100%;
        margin-top: 20px;
        text-align: center;
        max-width: 260px;
        &:hover {
          color: #23527c;
          text-decoration: underline;
        }
      }
      p {
        margin: 0;
      }
      .title {
        font-size: 26px;
        line-height: 60px;
        font-weight: 700;
        border-bottom: 1px solid #bfbfbf;
      }
      .fb-btn {
        background-position: 0 -40px;
        width: 150px;
        height: 28px;
        background-image: url(../images/icon_ui_components.png);
        margin: 5px auto 0;
        cursor: pointer;
      }
      .line {
        position: relative;
        text-align: center;
        margin: 30px 0;
        &:before {
          position: absolute;
          content: "";
          width: 96%;
          background-color: #f3f3f3;
          height: 1px;
          left: 0;
          top: 50%;
          margin-left: 2%;
        }
        span {
          background: #fff;
          width: 40px;
          display: inline-block;
          font-size: 14px;
          position: relative;
          z-index: 2;
        }
      }
    }
    a.login-btn {
      color: #626262;
      text-decoration: underline;
      font-size: 16px;
      margin: 5px;
      display: block;
    }
    .signup-data {
      .info {
        color: #ed1c24;
        margin-bottom: 20px;
        font-size: 16px;
      }
      .s {
        p,
        span {
          color: #ed1c24;
        }
      }
      select {
        appearance: none;
        -webkit-appearance: none;
        height: 40px;
        border: none;
        background: #e8e8e8 url(../images/icon_select.png) 94% center no-repeat;
        text-align: center;
        padding-right: 23px;
        padding-left: 5px;
      }
      label,div.label {
        margin-bottom: 20px;
        display: block;
        font-size: 0;
        >* {
          display: inline-block;
          vertical-align: top;
          font-size: 14px;
        }
        >p,
        >span:not(.t) {
          margin-top: 10px;
          width: 20%;
          padding-right: 10px;
        }
        >input[type="text"],
        >input[type="password"],
        .mul {
          width: 77%;
          position: relative;
        }
        .mul {
          display: inline-flex;
          flex-flow: row wrap;
          justify-content: space-between;
        }
        &.name input {
          width: 48%;
        }
        &.mobile {
          select {
            width: 36%;
          }
          input {
            width: 63%;
          }
        }
        &.address {
          input[name="zipcode"] {
            width: 25%;
            margin-bottom: 5px;
          }
          select[name="country"],
          select[name="province"] {
            width: 36%;
            margin-bottom: 5px;
          }
          select[name="city"],
          select[name="area"] {
            width: 49%;
            margin-bottom: 5px;
          }
          input[name="address"] {
            width: 100%;
          }
        }
        &.gender {
          .mul {
            align-items: center;
            justify-content: flex-start;
            span {
              vertical-align: middle;
              margin-right: 20px;
              margin-left: 5px;
            }
          }
        }
        &.birth select {
          width: 32%;
        }
        &.check {
          padding-left: 7%;
          text-align: left;
          input,
          span,
          a {
            margin-left: 5px;
            font-size: 16px;
            vertical-align: middle;
          }
          a {
            color: #ed1c24;
          }
        }
      }
    }
    .success-div{
      .text{
        height: 250px;
        text-align: center;
        padding: 0 20px;
        &:after{
          content:"";
          display: inline-block;
          height: 100%;
          vertical-align: middle;
        }
      }
      .info{
        display: inline-block;
        vertical-align: middle;
        font-size: 16px;
      }
    }
    .getpass-div {
      form {
        width: 300px;
        margin: 0 auto;
        input{
          margin-top: 40px;
          width: 100%;
          margin-bottom: 5px;
        }
        button{
          margin-top: 30px;
          margin-bottom: 30px;
        }
      }
    }
  }
}