@import "normalize";
@import "variable";
@import "color";
@import "font-face";
@import "rwd";

*{
    box-sizing: border-box;
}

html[lang=zh-TW] body{
    font-family: "Noto Sans TC", "Microsoft JhengHei", "HanHei TC", "MHei", "PingFang TC", "STHeitiTC-Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}
html[lang=zh-TW] body.en{
    font-family: 'Lato', sans-serif;
}
html[lang=en] body{
    font-family: 'Lato', sans-serif;
}

body, input, textarea, select, button{
    font-synthesis: none;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -moz-font-feature-settings: 'liga', 'kern';
    direction: ltr;
    text-align: left;
}

a{
    text-decoration: none;
    color: $color-black;
}

ol,ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

img{
    max-width: 100%;
}
