.header{
    width: 100%;
    height: 60px;
    background-color: white;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    /*box-shadow: 0 0 10px rgba(0,0,0,.2);*/
    .logo{
        width: 145px;
        height: 24px;
        background-image: url('../images/icon-logo@2x.png');
        background-size: 145px 24px;
        position: absolute;
        top: 20px;
        left: 20px;
        a{
            display: block;
            width: 145px;
            height: 24px;
            text-indent: -99999px;
        }
    }
    .search{
        width: 40px;
        height: 60px;
        background-image: url(../images/icon-search.png);
        background-size: 18px;
        background-position: center;
        background-repeat: no-repeat;
        position: absolute;
        right: 100px;
        top: 0;
    }
    .login{
        width: 40px;
        height: 60px;
        background-image: url(../images/icon-member.png);
        background-size: 14px 18px;
        background-position: center;
        background-repeat: no-repeat;
        position: absolute;
        right: 60px;
        top: 0;
    }
    @media(--pad){

    }
    @media(--desktop){
        height: 100px;
        .logo{
            width: 200px;
            background-image: url('../images/icon-logo.png');
            height: 32px;
            background-size: 200px 32px;
            top: 37px;
            left: 30px;
            a{
                width: 200px;
                height: 32px;
            }
        }
        .login{
            width: 30px;
            height: 100px;
            right: 85px;
            cursor: pointer;
        }
        .search{
            width: 30px;
            height: 100px;
            right: 125px;
            cursor: pointer;
        }
    }
    @media(--desktop-w){
        .logo{
            left: 55px;
        }
        .login{
            right: 105px;
        }
        .search{
            right: 145px;
        }
    }
    @media(--desktop-hd){
        .login{
            right: 55px;
        }
        .search{
            right: 95px;
            &:before{
                content: "|";
                font-size: 12px;
                position: absolute;
                top: 43px;
                left: -6px;
            }
        }
    }
}