.search__form{
    width: 100%;
    /*height: 60px;*/
    position: fixed;
    left: 0;
    top: 0;
    transition: transform .45s ease;
    transform: translate(0, 0);
    padding: 10px 20px;
    background-color: $color-gray-light;
    z-index: 98;
    &.is-open{
        transform: translate(0, 60px);
    }
    form{
        display: flex;
    }
    input[type="text"]{
        border: 1px solid $color-gray-light;
        height: 35px;
        padding-left: 10px;
        font-size: 14px;
        width: calc(100% - 100px);
    }
    input[type="submit"]{
        background-color: $color-black;
        color: white;
        border: none;
        height: 35px;
        padding: 0 20px;
        font-size: 14px;
        width: 100px;
        text-align: center;
    }
    @media(--desktop){
        padding: 20px 30px;
        &.is-open{
            transform: translate(0, 100px);
        }
    }
}

.search__result{
    .popup{
        .container{
            padding: 30px;
        }
    }
    &__text{
        p{
            font-size: 22px;
            letter-spacing: .05em;
            text-align: center;
            span{
                color: $color-red;
                padding: 0 5px;
                &:before{
                    content: '"';
                }
                &:after{
                    content: '"';
                }
            }
        }
    }
    .tabs{
        margin-bottom: 20px;
        padding: 0 20px;
        li{
            margin-bottom: 5px;
            text-transform: uppercase;
        }
        a{
            display: block;
            line-height: 35px;
            background-color: $color-gray;
            font-size: 14px;
            padding: 0 10px;
            letter-spacing: .05em;
            transition: background-color .45s ease, color .45s ease;
        }
        li.is-current a{
            background-color: $color-black;
            color: white;
        }
        span{
            color: $color-red;
            /*padding: 0 5px;*/
        }
    }
    .photo__list{
        padding: 0 20px;
        li{
            margin-bottom: 20px;
        }
        img{
            display: block;
            margin-bottom: 12px;
        }
        p{
            margin: 0;
        }
    }
    .video__list{
        padding: 0 20px;
        .play-button{
            width: 40px;
            height: 40px;
            cursor: pointer;
            border: 2px solid white;
            border-radius: 50%;
            position: absolute;
            left: 50%;
            top: 50%;
            margin: -20px 0 0 -20px;
            z-index: 3;
            &:before{
                content: "";
                width: 0; 
                height: 0; 
                border-top: 6px solid transparent;
                border-bottom: 6px solid transparent;
                border-left: 10px solid white;
                position: absolute;
                top: 50%;
                left: 50%;
                margin: -6px 0 0 -3px;
            }
        }
    }
    @media(--pad){
        .tabs{
            li{
                margin-bottom: 0;
                display: inline-block;
                margin-right: 10px;
            }
            li:last-child{
                margin-right: 0;
            }
            a{
                line-height: 50px;
                padding: 0 10px;
            }
        }
        .photo__list{
            padding: 0  0 0 20px;
            ol{
                display: inline-flex;
                flex-wrap: wrap;
            }
            li{
                width: calc((100% - 20px)/2);
                margin-right: 20px;
                margin-bottom: 20px;
            }
            li:nth-child(2n){
                margin-right: 0;
            }
        }
    }
    @media(--desktop){
        .tabs{
            padding: 0;
        }
        .photo__list{
            padding: 0;
            li{
                width: calc((100% - 40px)/ 3);
            }
            li:nth-child(2n){
                margin-right: 20px;
            }
            li:nth-child(3n){
                margin-right: 0;
            }
        }
        .video__list{
            padding: 0;
            li{
                width: calc((100% - 20px)/2);
            }
            li:nth-child(2n){
                margin-right: 0;
            }
        }
    }
    @media(--desktop-w){
        .photo__list{
            li{
                width: calc((100% - 60px)/4);
            }
            li:nth-child(3n){
                margin-right: 20px;
            }
            li:nth-child(4n){
                margin-right: 0;
            }
        }
        .video__list{
            li{
                width: calc((100% - 60px)/4);
            }
            li:nth-child(2n){
                margin-right: 20px;
            }
            li:nth-child(4n){
                margin-right: 0;
            }
        }
    }
    @media(--desktop-hhd){
        .photo__list{
            li{
                width: calc((100% - 120px)/4);
                margin-right: 30px;
            }
            li:nth-child(2n){
                margin-right: 30px;
            }
            li:nth-child(4n){
                margin-right: 30px;
            }
        }
        .video__list{
            li{
                width: calc((100% - 120px)/4);
                margin-right: 30px;
            }
            li:nth-child(2n){
                margin-right: 30px;
            }
            li:nth-child(4n){
                margin-right: 30px;
            }
        }
    }
}