.cooperation{
    .side-image{
        display: none;
    }
    .container{
        padding: 0 20px;
    }
    &__content{
        letter-spacing: .05em;
        margin-bottom: 30px;
        .tabs{
            margin-bottom: 20px;
            li{
                font-family: 'Lato', sans-serif;
                margin-bottom: 5px;
                text-transform: uppercase;
            }
            a{
                display: block;
                line-height: 35px;
                background-color: $color-gray;
                font-size: 14px;
                padding: 0 10px;
                transition: background-color .45s ease, color .45s ease;
            }
            li.is-current a{
                background-color: $color-black;
                color: white;
            }
        }
        h1{
            font-size: 22px;
            text-align: center;
            margin: 30px 0;
        }
        h2{
            font-size: 18px;
            text-align: center;
        }
        h3{
            font-size: 16px;
            margin: 15px 0;
            /*font-weight: normal;*/
        }
        .paragraph{
            line-height: 1.5;
            p{
                font-size: 14px;
            }
            a{
                color: $color-red;
            }
        }
        ul{
            list-style: decimal;
            margin-left: 1.2em;
            li{
                font-size: 14px;
            }
        }
        .iframe{
            position: relative;
            padding-bottom: 56.25%;
            height: 0;
            overflow: hidden;
            iframe{
                position: absolute;
                top:0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
    }
    &__advantage{
        .block{
            margin-bottom: 20px;
            p{
                margin: 0;
                color: $color-gray-dark;
                font-size: 14px;
            }
        }
    }
    &__format{
        .format__block{
            margin-bottom: 40px;
            .image{
                margin-bottom: 5px;
            }
            .info{
                .title{
                    margin-bottom: 10px;
                    font-weight: 500;
                }
                p{
                    margin: 0;
                    font-size: 14px;
                }
            }
        }
    }
    @media(--pad){
        &__content{
            .tabs{
                li{
                    margin-bottom: 0;
                    display: inline-block;
                    margin-right: 10px;
                }
                a{
                    line-height: 50px;
                    padding: 0 20px;
                }
            }
            h1{
                font-size: 32px;
                margin: 0;
                padding: 50px 0 30px 0;
            }
            h2{
                font-size: 24px;
                margin: 30px 0;
            }
            h3{
                font-size: 20px;
            }
            .paragraph p{
                font-size: 16px;
            }
        }
        &__format{
            .format__block{
                display: flex;
                align-items: center;
                .image{
                    width: 50%;
                    margin-bottom: 5px;
                }
                .info{
                    flex: 1;
                    padding-left: 30px;
                    padding-top: 30px;
                    max-width: 250px;
                }
            }
        }
    }
    @media(--desktop){
        .side-image{
            display: block;
            position: fixed;
            /*width: calc(40% - 35px);*/
            width: calc(((920)/1900*100)% - 60px);
            height: 100vh;
            right: 30px;
            top: 0;
            background-color: white;
            background-size: 80%;
            background-repeat: no-repeat;
            background-position: center;
        }
        .main-wrapper{
            // padding: 0 30px;
            .container{
                padding: 0;
                /*width: calc(60% - 35px);*/
                width: calc((980)/1900*100)%;
                margin-left: 30px;
            }
        }
        &__content{
            margin-bottom: 0;
            width: 100%;
            background-color: #f9f9f9;
            padding: 0 50px;
            padding-bottom: 50px;
            .tabs{
                li{
                    margin-bottom: 10px;
                }
            }
            h3{
                margin: 20px 0;
            }
        }
        .subscribe{
            margin-left: 30px;
            width: calc((980)/1900*100)%;
        }
        #footer{
            margin-left: 30px;
            width: calc((980)/1900*100)%;
        }
    }

    @media(--desktop-w){
        .side-image{
            width: calc(((920)/1900*100)% - 110px);
        }
        .main-wrapper{
            .container{
                margin-left: 55px;
            }
        }
        &__content{
            padding: 0 50px 50px 50px;
        }
        .subscribe{
            margin-left: 55px;
        }
        #footer{
            margin-left: 55px;
            width: calc((980)/1900*100)%;
        }
    }
}