body{
    &:after{
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: fixed;
        z-index: -1;
        background: black;
        opacity: 0;
        left: 0;
        top: 0;
    }
}

/* menu open */
.menu-open{
    &:after{
        opacity: .8;
        z-index: 8;
        transition: opacity .45s ease .1s;
    }
    .off-canvas{
        transform: translate(0,0);
        transition: transform .45s cubic-bezier(.7,0,.3,1);
    }
    @media(--desktop-hd){
        &:after{
            opacity: 0;
            z-index: -1;
        }
    }
}

.off-canvas{
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: white;
    right: 0;
    top: 0;
    z-index: 99;
    transform: translate(100%, 0);
    transition: transform .45s cubic-bezier(.7,0,.3,1);
    .nav{
        padding: 0 20px;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;
    }
    nav{
        padding: 60px 10px 0 10px;
        border-bottom: 1px solid $color-border;
        margin-bottom: 10px;
        ol{}
        li{
            font-family: 'Lato', sans-serif;
            margin-bottom: 20px;
        }
        a{
            text-transform: uppercase;
        }
    }
    .socialMedia{
        display: flex;
        padding: 0 10px;
        align-content: center;
        margin-bottom: 30px;
        .title{
            text-transform: uppercase;
            font-family: 'Lato', sans-serif;
            margin-right: 10px;
            line-height: 30px;
        }
        li{
            display: inline-block;
            width: 30px;
            height: 30px;
        }
        a{
            display: block;
            width: 30px;
            height: 30px;
            background-color: $color-gray-light;
            text-indent: -99999px;
            border-radius: 50%;
            background-size: 30px;
            background-position: center;
        }
        a.facebook{
            background-image: url(../images/icon-facebook.png);
        }
        a.youtube{
            background-image: url(../images/icon-youtube.png);
        }
    }
    .footer{
        padding: 0 10px;
        ol{
            
        }
        li{
            display: inline-block;
            width: 40%;
            margin-bottom: 10px;
            font-size: 14px;
        }
    }
    @media(--pad){
        width: 40%;
    }
    @media(--desktop){
        .nav{

        }
        nav{
            li{
                position: relative;
                &:after{
                    content: "";
                    display: block;
                    width: 0%;
                    height: 1px;
                    background-color: $color-red;
                    transition: width .25s ease;
                    position: absolute;
                    left: 0;
                    bottom: -5px;
                }
                &.is-current,
                &:hover{
                    &:after{
                        width: 100%;
                    }
                }
            }
            a{

            }
        }
    }
    @media(--desktop-hd){
        transform: translate(0, 0);
        width: 100%;
        height: 0;
        background-color: transparent;
        .footer{
            display: none;
        }
        .socialMedia{
            position: absolute;
            right: 145px;
            top: 36px;
            .title{
                position: relative;
                cursor: pointer;
                font-size: 14px;
                &:after{
                    content: "";
                    display: block;
                    width: 0;
                    height: 0;
                    border-left: 4px solid transparent;
                    border-right: 4px solid transparent;
                    border-top: 6px solid $color-black;
                    position: absolute;
                    right: -14px;
                    top: 13px;
                    transition: transform .45s ease;
                    transform: rotate(0);
                }
            }
            &:hover{
                .socialMedia__list{
                    display: block;
                }
                .title{
                    &:after{
                        transform: rotate(-180deg);
                    }
                }
            }
            &__list{
                display: none;
                position: absolute;
                top: 30px;
                left: 5px;
                ol{
                    display: block;
                    background-color: $color-gray-light;
                    /*width: 90px;*/
                    height: 40px;
                }
                a{
                    background-position: center;
                    width: 40px;
                    height: 40px;
                    margin: 0 auto;
                    background-repeat: no-repeat;
                    background-color: $color-gray-light;
                    border-radius: 0;
                    transition: background-color .45s ease;
                    &:hover{
                        background-color: $color-gray;
                    }
                }
            }
        }
        .nav{
            overflow: inherit;
            height: 0;
        }
        nav{
            border-bottom: none;
            padding: 40px 0 0 0;
            display: inline-block;
            position: absolute;
            right: 240px;
            li{
                display: inline-block;
                margin-bottom: 0;
                margin-left: 10px;
                font-size: 14px;
                letter-spacing: .05em;
            }
        }
    }
}