#footer{
    background-color: $color-gray-light;
    padding: 15px 0;
    nav{
        display: none;
    }
    .service{
        width: 80%;
        margin: 0 auto 10px;
        text-align: center;
        li{
            display: inline-block;
            padding: 0 5px;
            font-size: 12px;
        }
    }
    .copyright{
        font-size: 12px;
        text-align: center;
        font-family: 'Lato', sans-serif;
    }
    .container{
        position: relative;
    }
    .goTop{
        top: -15px;
        right: 0;
        width: 40px;
        height: 40px;
        background-color: $color-black;
        position: absolute;
        background-image: url(../images/icon-arrow-left.png);
        background-size: 9px 16px;
        background-position: center;
        background-repeat: no-repeat;
        transform: rotate(90deg);
        cursor: pointer;
        @media(--pad){
            top: 0px;
            right: 40px;
        }
        @media(--desktop){
            right: 10px;
        }
        @media(--desktop-w){
            right: 30px;
        }
        @media(--desktop-hd){
            right: 55px;
        }
    }
    @media(--pad){
        padding: 40px 0;
        nav{
            display: block;
            text-align: center;
            margin-bottom: 30px;
            li{
                display: inline-block;
                &:after{
                    content: '|';
                    font-weight: bolder;
                    padding: 0 25px;
                }
            }
            li:last-child:after{
                display: none;
            }
        }
        .service{
            margin-bottom: 20px;
            li{
                font-size: 14px;
            }
        }
        .copyright{
            font-size: 14px;
        }
    }
}