.popup{
    display: none;
    .container{
        position: absolute;
        z-index: 2;
        width: 100%;
        height: 100%;
        overflow: auto;
    }
    .close-button{
        width: 40px;
        height: 40px;
        position: absolute;
        top: 20px;
        right: 0;
        z-index: 10;
        cursor: pointer;
        &:before,
        &:after{
            content: "";
            display: block;
            width: 32px;
            height: 2px;
            background-color: white;
            position: absolute;
            top: 50%;
            left: 50%;
            margin: -1px 0 0 -16px;
        }
        &:before{
            transform: rotate(45deg);
        }
        &:after{
            transform: rotate(-45deg);
        }
    }
    &__video{
        .content{
            position: relative;
            width: 100%;
            max-width: 1280px;
            margin: 0 auto;
            padding-top: 70px;
        }
        .iframe{
            position: relative;
            padding-bottom: 56.25%;
            height: 0;
            overflow: hidden;
            margin-bottom: 20px;
            iframe{
                position: absolute;
                top:0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
    }
    &__article{
        .container{
            
        }
        &__slider{
            position: fixed;
            width: 100%;
            height: 100%;
            li{
                height: calc(100% - 180px);
                background-size: 0;
                background-repeat: no-repeat;
                background-position: center bottom;
                display: inline-flex;
                flex-wrap: wrap;
                align-items: center;
                @media(--desktop){
                    background-size: contain;
                }
            }
            .bx-wrapper{
                position: absolute;
                width: 100%;
                height: 100%;
                // display: inline-flex;
                // flex-wrap: wrap;
                // align-items: center;
                .bx-viewport{
                    position: absolute !important;
                    height: 100% !important;
                    width: 100%;
                    ul{
                        height: 100%;
                        display: flex;
                        align-items: center;
                    }
                    li{
                        padding: 0 20px;

                    }
                    @media(--desktop){
                        li{
                            padding: 0;
                        }
                    }
                }
                img{
                    display: inline-block;
                }
                .bx-controls{
                    position: fixed;
                    top: 50%;
                    width: 100%;
                    z-index: 99;
                    a{
                        display: inline-block;
                        width: 40px;
                        height: 40px;
                        background-size: 14px 25px;
                        background-position: center;
                        background-repeat: no-repeat;
                        text-indent: -99999px;
                        position: absolute;
                        top: 0;
                    }
                    .bx-prev{
                        left: 0;
                        background-image: url(../images/icon-arrow-left.png);
                    }
                    .bx-next{
                        right: 0;
                        background-image: url(../images/icon-arrow-right.png);
                    }
                    @media(--desktop){
                        bottom: inherit;
                        // position: static;
                        // top: 0;
                        a{
                            bottom: inherit;
                            // top: 50%;
                        }
                        .bx-prev{
                            left: 40px;
                        }
                        .bx-next{
                            right: 40px;
                        }
                    }
                }
            }
            .cover{
                text-align: center;
                width: 100%;
                img{
                    margin: 0 auto;
                }
                @media(--desktop){
                    display: none;
                }
            }
            .text{
                position: absolute;
                color: white;
                width: 100%;
                left: 0;
                bottom: 0;
                text-align: center;
                @media(--desktop){
                    background-color: #000;
                    padding: 10px;
                }
            }
        }
        &__thumb{
            position: absolute;
            bottom: 0;
            left: 0;
            display: none;
            a{
                display: inline-block;
                width: 80px;
                height: 80px;
                background-size: cover;
                background-position: center top;
                background-repeat: no-repeat;
            }
            @media(--desktop){
                display: block;
                width: 100%;
                background-color: white;
                padding: 0;
                height: 90px;
                padding: 5px;
                a{
                    margin-right: 5px;
                }
            }
        }
        .close-button{
            position: fixed;
            right: 20px;
        }
        
        .content{
            position: absolute;
            height: 100%;
            width: 100%;
            // max-width: 500px;
            // margin: 0 auto;
            .image{
                text-align: center;
                img{
                    display: block;
                    margin: 0 auto;
                }
            }
            @media(--desktop){
                .image{
                    // max-width:600px;
                    // margin: 0 auto;
                }
            }
        }
        .info{
            color: white;
            font-size: 14px;
            padding: 0 20px;
            text-align: center;
            // position: absolute;
            // width: 100%;
            // bottom: 0;
            @media(--desktop){
                max-width: 500px;
                margin: 0 auto;
            }
        }
        .slideNum{
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            padding: 20px 0;
            color: white;
            text-align: center;
            .currentNum{
                &:after{
                    content: "/";
                    padding: 0 10px;
                }
            }
            @media(--desktop){
                position: fixed;
                bottom: inherit;
                top: 10px;
            }
        }
    }
    &__photo{
        .container{
            display: inline-flex;
            flex-wrap: wrap;
            align-items: center;
        }
        .close-button{
            position: fixed;
            right: 20px;
        }
        .content{

        }
        .photo{
            height: 100%;
            background-size: 0;
            background-repeat: no-repeat;
            background-position: center bottom;
            
            img{
                display: block;
            }
            @media(--desktop){
                background-size: contain;
                position: absolute;
                height: 80%;
                width: 80%;
                left: 10%;
                top: 10%;
                img{
                    display: none;
                }
            }
        }
    }
}
.popup.is-open{
    display: block;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 100;
    &:after{
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,.9);
        left: 0;
        top: 0;
        z-index: 1;
    }
}
