.subscribe{
    background-color: $color-gray;
    padding: 10px 20px;
    .title{
        font-weight: bold;
        text-align: center;
        margin-bottom: 10px;
        text-transform: uppercase;
    }
    form{
        display: flex;
    }
    input[type="text"]{
        width: 70%;
        height: 35px;
        background-color: white;
        border: none;
        font-size: 14px;
        padding-left: 10px;
        border-radius: 0;
    }
    input[type="button"], input[type="submit"]{
        flex: 1;
        height: 35px;
        text-align: center;
        background-color: $color-black;
        border: none;
        color: white;
        font-size: 14px;
        font-weight: bold;
        border-radius: 0;
    }
    @media(--pad){
        padding: 20px 0 30px 0;
        .title{
            font-size: 24px;
            margin-bottom: 20px;
        }
        form{
            width: 415px;
            margin: 0 auto;
        }
        input{
            font-size: 16px;
        }
    }
    @media(--desktop){
        margin-left: 40%;
    }
    @media(--desktop-w){
        
    }
}