.list{
    .main-wrapper{
        display:block;
        flex-wrap: wrap;
        /*flex-direction: column-reverse;*/
        .container{
            flex: 1;
        }
    }
    .side{
        width: 100%;
        margin-bottom: 20px;
        &__ad{
            padding: 0 20px;
            margin-bottom: 10px;
        }
    }
    .popular__list{
        .title{
            font-size: 18px;
            text-align: center;
            padding: 20px 0;
        }
        li{
            position: relative;
        }
        
        .info{
            position: absolute;
            color: white;
            left: 0;
            bottom: 0;
            padding: 15px;
            background-color: rgba(0,0,0,.7);
            width: 100%;
            .type{
                color: white;
            }
            a{
                color:white;
            }
        }
    }
    @media(--pad){
        .main-wrapper{
            display: inline-flex;
            .container{
                flex: 1;
            }
        }
        .side{
            width: 35%;
        }
        .news__list{
            padding: 0 0 0 20px;
            li{
                width: calc((100% - 20px) / 2);
                margin-right: 20px;
                &:nth-child(3){
                    margin-right: 20px;
                }
                &:nth-child(2n){
                    margin-right: 0;
                }
            }
        }
        .popular__list{
            .cover{
                margin-bottom: 0;
            }
            li{
                width: 100%;
                margin-right: 0;
            }
        }
    }
    @media(--desktop){
        .main-wrapper{
            padding: 100px 30px 0 30px;
            .container{
                flex: 1;
            }
        }
        .side{
            &__ad{
                padding: 0 0 0 20px;
            }
            .title{
                text-align: left;
                padding: 0;
                font-size: 24px;
                margin-top: 20px;
                margin-bottom: 10px;
            }
        }
        .news__list{
            padding: 0;
        }
        .popular__list{
            padding: 0 20px;
            .cover{
                margin-bottom: 0;
            }
            li:first-child{
                display: block;
                padding-bottom: 0;
                .info{
                    position: absolute;
                    bottom: 0;
                    padding: 10px;
                    background-color: rgba(0,0,0,.7);
                    color: white;
                    a{                        
                    color: white;
                    }
                    .type{
                        display: none;
                    }
                }
            }
            li{
                display: flex;
                align-items: center;
                border-bottom: 1px solid $color-gray-light;
                padding-bottom: 10px;
                margin-bottom: 10px;
                a{
                    width: 50%;
                }
                .info{
                    flex: 1;
                    position: static;
                    padding: 0 10px;
                    background-color: transparent;
                    color: $color-black;
                    .type{
                        display: block;
                        color: $color-gray-dark;
                    }
                }
            }
        }
        .news__list{
            li{
                width: calc((100% - 40px)/3);
                margin-right: 20px;
                &:nth-child(2n){
                    margin-right: 20px;
                }
                &:nth-child(3n){
                    margin-right: 0;
                }
            }
        }
    }
    @media(--desktop-w){
        .main-wrapper{
            padding: 100px 55px 0 55px;
        }
        .side{
            width: 25%;
        }
        .news__list{
            li{
                width: calc((100% - 60px)/4);
                margin-right: 20px;
                &:nth-child(3n){
                    margin-right: 20px;
                }
                &:nth-child(4n){
                    margin-right: 0px;
                }
            }
        }
        .popular__list{
            padding: 0 0 0 20px;
            li{
                a{
                    width: 30%;
                }
            }
        }
    }
    @media(--desktop-hd){

    }
    @media(--desktop-hhd){
        .side{
            width: calc(335/1790*100)%;
            &__ad{
                padding: 0;
            }
        }
        .news__list{
            li{
                width: calc((100% - 120px)/4);
                margin-right: 30px;
                &:nth-child(2n),
                &:nth-child(3n){
                    margin-right: 30px;
                }
                &:nth-child(4n){
                    margin-right: 30px;
                }
            }
        }
        .popular__list{
            padding: 0;
        }
    }
}