.video{
    .play-button{
        width: 40px;
        height: 40px;
        cursor: pointer;
        border: 2px solid white;
        border-radius: 50%;
        position: absolute;
        left: 50%;
        top: 50%;
        margin: -20px 0 0 -20px;
        z-index: 3;
        &:before{
            content: "";
            width: 0; 
            height: 0; 
            border-top: 6px solid transparent;
            border-bottom: 6px solid transparent;
            border-left: 10px solid white;
            position: absolute;
            top: 50%;
            left: 50%;
            margin: -6px 0 0 -3px;
        }
    }
    .main-wrapper{
        margin-bottom: 30px;
    }
    &__top{
        // padding: 0 20px;
        margin-bottom: 20px;
        
        .cover{
            position: relative;
            img{
                position: relative;
                z-index: 1;
                display: block;
            }
            .iframe{
                position: absolute;
                padding-bottom: 56.46%;
                height: 0;
                overflow: hidden;
                left: 0;
                top: 0;
                z-index: 3;
                width: 100%;
                iframe{
                    background: #000;
                    position: absolute;
                    top:0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                }
            }
        }
        .info{
            letter-spacing: .05em;
            line-height: 1.5;
            background-color: $color-gray-light;
            padding: 10px;
            .subject{
                margin-bottom: 5px;
            }
            .intro{
                font-size: 12px;
                color: $color-gray-dark;
                margin-bottom: 5px;
            }
            .date{
                font-size: 12px;
                color: $color-gray-dark;
                text-align: right;
                color: $color-gray-dark;
            }
        }
    }
    &__list{
        li{
            margin-bottom: 20px;
        }
        .cover{
            position: relative;
            img{
                display: block;
            }
        }
        .info{
            letter-spacing: .05em;
            line-height: 1.5;
            background-color: $color-gray-light;
            padding: 10px;
            .subject{
                margin-bottom: 5px;
            }
            .intro{
                font-size: 12px;
                color: $color-gray-dark;
                margin-bottom: 5px;
            }
            .date{
                font-size: 12px;
                color: $color-gray-dark;
                text-align: right;
                color: $color-gray-dark;
            }
        }
    }
    .container{
        padding: 0 20px;
    }
    .popup__video{
        .container{
            display: flex;
            align-items: center;
        }
    }
    &__detail{
        max-width: 1280px;
        width: 100%;
        margin: 50px auto;
        .iframe{
            position: relative;
            padding-bottom: 56.25%;
            height: 0;
            width: 100%;
            margin-bottom: 10px;
            iframe{
                position: absolute;
                top:0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
        .info{
            letter-spacing: .05em;
            line-height: 1.5;
            // padding: 10px;
            .intro{
                font-size: 12px;
                color: $color-gray-dark;
            }
            .date{
                font-size: 12px;
                color: $color-gray-dark;
            }
        }
    }
    @media(--pad){
        .play-button{
            width: 65px;
            height: 65px;
            margin: -32.5px 0 0 -32.5px;
            &:before{
                border-top: 12px solid transparent;
                border-bottom: 12px solid transparent;
                border-left: 20px solid white;
                margin: -12px 0 0 -6px;
            }
        }
        &__top{
            background-color: $color-gray-light;
            padding-bottom: 30px;
            .info{
                position: relative;
                padding: 10px 20px;;
                .subject{
                    font-size: 18px;
                }
                .intro{
                    font-size: 14px;
                }
                .date{
                    text-align: left;
                    font-size: 14px;
                }
            }
        }
        &__list{
            ol{
                display: inline-flex;
                flex-wrap: wrap;
            }
            li{
                width: calc((100% - 20px) / 2);
                margin-right: 20px;
            }
            li:nth-child(2n){
                margin-right: 0;
            }
            .info{
                background-color: transparent;
                padding: 10px 0;
                .subject{
                    font-size: 18px;
                }
                .intro{
                    font-size: 14px;
                }
                .date{
                    font-size: 14px;
                    text-align: left;
                }
            }
        }
        &__detail{
            .info{
                .subject{
                    font-size: 18px;
                    margin-bottom: 5px;
                }
                .intro{
                    font-size: 14px;
                    margin-bottom: 5px;
                }
                .date{
                    font-size: 14px;
                }
            }
        }
    }
    @media(--desktop){
        &__top{
            margin: 0;
            padding: 0 30px 30px 30px;
            & > a{
                display: block;
                max-width: 1240px;
                margin: 0 auto;
            }
            .info{
                padding: 10px 0;
            }
        }
        .container{
            padding: 30px;
        }
        &__list{
            li{
                width: calc((100% - 60px)/4);
            }
            li:nth-child(2n){
                margin-right: 20px;
            }
            li:nth-child(4n){
                margin-right: 0;
            }
        }
    }
    @media(--desktop-w){
        &__top{
            padding: 0 55px 55px 55px;
        }
        .container{
            padding: 30px 55px 30px 55px;
        }
    }
}