.menu-toggle{
    position: fixed;
    width: 60px;
    height: 60px;
    right: 0;
    top: 0;
    z-index: 100;
    overflow: hidden;
    &:before,
    &:after{
        content: "";
        display: block;
        width: 20px;
        height: 4px;
        background-color: $color-black;
        position: absolute;
        left: 50%;
        margin-left: -10px;
        transition: transform .25s ease;
    }
    &:before{
        top: 20px;
    }
    &:after{
        bottom: 20px;
    }
    span{
        display: block;
        width: 20px;
        height: 4px;
        position: absolute;
        left: 50%;
        top: 50%;
        margin: -2px 0 0 -10px;
        &:before,
        &:after{
            content: "";
            display: block;
            width: 20px;
            height: 4px;
            background-color: $color-black;
            position: absolute;
            top: 0;
            left: 0;
            transition: transform .45s ease ;
        }
    }
    @media(--pad){

    }
    @media(--desktop){
        width: 30px;
        right: 35px;
        height: 100px;
        cursor: pointer;
        &:before,
        &:after{
            width: 30px;
            margin-left: -15px;
        }
        &:before{
            top: 40px;
        }
        &:after{
            bottom: 40px;
        }
        span{
            width: 30px;
            margin: -2px 0 0 -15px;
            &:before,
            &:after{
                width: 30px;
            }
        }
    }
    @media(--desktop-w){
        right: 55px;
    }
    @media(--desktop-hd){
        display: none;
    }
}

/* menu open */
.menu-open{
    .menu-toggle{
        &:before{
            transform: translate(250%, 0);
        }
        &:after{
            transform: translate(-250%, 0);
        }
        span{
            &:before,
            &:after{
                transition: transform .45s ease .3s;
            }
            &:before{
                transform: rotate(45deg);
            }
            &:after{
                transform: rotate(-45deg);
            }
        }
    }
}