.about,
.privacy{
    .side-image{
        display: none;
    }
    .container{
        padding: 0 20px;
    }
    &__content{
        letter-spacing: .05em;
        margin-bottom: 30px;
        h1{
            font-size: 22px;
            text-align: center;
            margin: 30px 0;
            font-weight: 400;
        }
        h2{
            font-size: 18px;
            font-weight: 500;
            text-align: center;
        }
        h3{
            font-size: 16px;
            margin: 0 0 5px 0;
            font-weight: normal;
        }
        .paragraph{
            line-height: 26px;
            font-weight: 400;
            p{
                font-size: 14px;
            }
        }
    }
    &__advantage{
        .block{
            margin-bottom: 20px;
            p{
                margin: 0;
                color: $color-gray-dark;
                font-size: 14px;
            }
        }
    }
    @media(--pad){
        .container{
            padding: 0 30px;
        }
        &__content{
            h1{
                font-size: 32px;
                margin: 0;
                padding: 50px 0 0 0;
            }
            h2{
                font-size: 24px;
                margin: 30px 0;
            }
            h3{
                font-size: 20px;
            }
            .paragraph p{
                font-size: 16px;
            }
        }
    }
    @media(--desktop){
        .side-image{
            display: block;
            position: fixed;
            /*width: calc(40% - 35px);*/
            // width: calc(((920)/1900*100)% - 60px);
            width: calc(50% - 30px);
            height: 100vh;
            right: 30px;
            top: 0;
            background-color: white;
            background-size: 80%;
            background-repeat: no-repeat;
            background-position: center;
        }
        .main-wrapper{
            // padding: 0 30px;
            .container{
                padding: 0;
                /*width: calc(60% - 35px);*/
                // width: calc((980)/1900*100)%;
                width: calc(50% - 30px);
                margin-left: 30px;
            }
        }
        &__content{
            width: 100%;
            background-color: #f9f9f9;
            padding: 0 30px 30px 30px;
            margin-bottom: 0;
        }
        &__advantage{
            display: inline-flex;
            flex-wrap: wrap;
            .block{
                width: calc(50% - 10px);
            }
            .block:nth-child(2n+1){
                margin-right: 20px;
            }
            .block:nth-last-child(1){
                width: 100%;
            }
        }
        .subscribe{
            margin-left: 30px;
            // width: calc((980)/1900*100)%;
            width: 50%;
        }
        #footer{
            margin-left: 30px;
            // width: calc((980)/1900*100)%;
            width: 50%;
        }
    }
    @media(--desktop-w){
        .side-image{
            // width: calc(((920)/1900*100)% - 110px);
            width: calc(50% - 55px);
        }
        .main-wrapper{
            .container{
                margin-left: 55px;
            }
        }
        &__content{
            padding: 0 50px 50px 50px;
        }
        .subscribe{
            margin-left: 55px;
        }
        #footer{
            margin-left: 55px;
            // width: calc((980)/1900*100)%;
        }
    }
}