.people{
    .container{
        padding: 0 20px;
    }
    &__content{
        .shareMedia{
            margin: 30px 0;
        }
        .cover{

        }
        .intro{
            letter-spacing: .05em;
            line-height: 1.5;
            .name{
                font-size: 22px;
                margin: 15px 0;
                text-align: center;
            }
            .info{
                font-size: 14px;
            }
            .tag{
                margin: 10px 0;
                a{
                    display: inline-block;
                    padding: 0 10px;
                    line-height: 30px;
                    margin-right: 10px;
                    margin-bottom: 10px;
                    font-size: 12px;
                    background-color: $color-gray-light;
                    transition: background-color .45s ease;
                    &:hover{
                        background-color: $color-gray;
                    }
                }
            }
        }
    }
    &__post{
        padding: 0;
    }
    @media(--pad){
        margin-top: 50px;
        &__content{
            display: flex;
            align-items: center;
            margin-bottom: 50px;
            .cover{
                width: 45%;
            }
            .intro{
                flex: 1;
                padding: 0 50px;
                .name{
                    font-size: 28px;
                }
                .tag{
                    margin: 20px 0;
                    a{
                        font-size: 14px;
                        min-width: 100px;
                        padding: 0 15px;
                        text-align: center;
                    }
                }
            }
        }
    }
    @media(--desktop){
        .main-wrapper{
            > .container{
                max-width: 1070px;
                margin: 0 auto;
            }
        }
        &__content{
            margin-bottom: 80px;
            .cover{
                width: calc(520/1070*100)%;
            }
            .intro{
                flex: 1;
                padding: 0 75px;
                .name{
                    font-size: 32px;
                }
                .tag{
                    margin: 30px 0 40px 0;
                }
            }
        }
        &__post{
            margin-bottom: 100px;
            .cover{
                margin-bottom: 20px;
            }
            li{
                width: calc((100% - 60px)/4);
                margin-right: 20px;
                &:nth-child(3n){
                    margin-right: 20px;
                }
                &:nth-child(4n){
                    margin-right: 0;
                }
            }
        }
    }

}