.article{
    &__slider{
        margin-bottom: 30px;
        position: relative;
        .slider__cover{
            background-size: cover;
            background-position: center top;
            background-repeat: no-repeat;
        }
        .slider__thumb{
            position: absolute;
            left: 0;
            bottom: 15px;
            .bx-wrapper{
                background-color: white;
                padding: 5px;
            }    
            li{
                display: inline-block;
                width: 70px;
                height: 80px;
                background-size: cover;
                background-position: center top;
            }
            /*a{
                display: block;
                width: 70px;
                height: 80px;
                background-size: cover;
                background-position: center top;
            }*/
        }
        .bx-controls{
            position: absolute;
            bottom: 90px;
            left: 0;
            a{
                display: inline-block;
                width: 40px;
                height: 40px;
                background-size: 14px 25px;
                background-color: $color-black;
                background-position: center;
                background-repeat: no-repeat;
                text-indent: -99999px;
            }
            .bx-prev{
                background-image: url(../images/icon-arrow-left.png);
            }
            .bx-next{
                background-image: url(../images/icon-arrow-right.png);
            }
        }
    }
    &__content{
        .subject{
            font-size: 22px;
            font-weight: 300;
            text-align: center;
            margin-bottom: 10px;
        }
        .date{
            font-family: 'Lato', sans-serif;
            text-transform: uppercase;
            text-align: center;
            span{
                padding: 0 4px;
                font-size: 14px;
                display: inline-block;
                vertical-align: middle;
                color: $color-gray-dark;
            }
            span:first-child{
                padding: 0;
            }
            span:first-child:after{
                content: "";
                display: inline-block;
                vertical-align: middle;
                margin: 0 5px 3px 10px;
                width: 1px;
                height: 12px;
                background-color: $color-red;
            }
            a{
                color: $color-gray-dark;
            }
        }
        .date ~ .shareMedia{
            padding: 35px 0;
        }
    }
    &__section{
        padding: 0 20px;
        letter-spacing: .05em;
        line-height: 1.5;
        h2{
            font-size: 18px;
            font-weight: 400;
            text-align: center;
            margin: 30px 0;
        }
        p{
            font-size: 14px;
        }
        .post__image{
            img{display: block;}
            .note{
                padding: 10px 0;
                color: $color-gray-dark;
                font-size: 12px;
                font-family: 'Lato', sans-serif;
                border-bottom: 1px solid $color-gray;
            }
        }
        .title{
            font-size: 18px;
            font-family: 'Lato', sans-serif;
            font-weight: 300;
            text-align: center;
        }
        .tag{
            padding: 20px 0;
            a{
                display: inline-block;
                padding: 0 5px;
                font-size: 12px;
                line-height: 30px;
                font-family: 'Lato', sans-serif;
                background-color: $color-gray-light;
                margin: 5px;
                transition: background-color .45s ease;
            }
            a:hover{
                background-color: $color-gray-dark;
            }
        }
    }
    .fb-comment{
        padding: 0 20px;
    }
    .section__share{
        margin: 30px 0;
        .title{
            margin-bottom: 30px;
        }
    }
    .section__tag{
        margin-bottom: 30px;
    }
    .author{
        padding: 20px 0;
        margin: 0 20px 30px 20px;
        border-top: 1px solid $color-gray;
        border-bottom: 1px solid $color-gray;
        display: flex;
        &__cover{
            width: 100px;
        }
        &__intro{
            flex: 1;
            padding: 0 0 0 10px;
            letter-spacing: .05em;
            line-height: 1.5;
            .name{
                font-size: 12px;
                position: relative;
                display: inline-block;
                margin-bottom: 10px;
                text-transform: uppercase;
                vertical-align: top;
                &:after{
                    content: "";
                    display: inline-block;
                    width: 100%;
                    height: 1px;
                    background-color: $color-black;
                    position: absolute;
                    bottom: -2px;
                    left: 0;
                }
            }
            .text{
                font-size: 12px;
            }
        }
    }
    &__recommend{
        margin-bottom: 20px;

        .cover{
            img{
                width: 100%;
            }
        }
        .bx-controls{
            a{
                display: inline-block;
                width: 30px;
                height: 30px;
                background-color: $color-black;
                background-size: 9px 16px;
                background-position: center;
                background-repeat: no-repeat;
                text-indent: -99999px;
                position: absolute;
                top: 40%;
            }
            .bx-prev{
                left: -15px;
                background-image: url(../images/icon-arrow-left.png);
            }
            .bx-next{
                right: -15px;
                background-image: url(../images/icon-arrow-right.png);
            }
        }
    }
    &__video{
        padding: 0 20px;
        margin-bottom: 30px;
        .title{
            font-size: 18px;
            margin-bottom: 10px;
            text-align: center;
        }
        .cover{
            position: relative;
            img{
                position: relative;
                z-index: 1;
                display: block;
            }
            .iframe{
                position: absolute;
                padding-bottom: 56.4%;
                height: 0;
                overflow: hidden;
                left: 0;
                top: 0;
                z-index: 3;
                width: 100%;
                iframe{
                    background: #000;
                    position: absolute;
                    top:0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                }
            }
        }
        .info{
            .subject{
                padding-bottom: 10px;
            }
            padding: 10px 0;
            text-align: center;
            background-color: $color-gray-light;
            & > span{
                font-family: 'Lato', sans-serif;

            }
        }
        .play-button{
            width: 40px;
            height: 40px;
            cursor: pointer;
            border: 2px solid white;
            border-radius: 50%;
            position: absolute;
            left: 50%;
            top: 50%;
            margin: -20px 0 0 -20px;
            z-index: 2;
            &:before{
                content: "";
                width: 0; 
                height: 0; 
                border-top: 6px solid transparent;
                border-bottom: 6px solid transparent;
                border-left: 10px solid white;
                position: absolute;
                top: 50%;
                left: 50%;
                margin: -6px 0 0 -3px;
            }
        }
        .thumb{
            display: none;
            ol{
                display: inline-flex;
                flex-wrap: wrap;
                /*transform: translate3d(0px, 0px, 0px) !important;*/
            }
            li{
                position: relative;
                /*width: auto !important;*/
                &:after{
                    content: "";
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    background-color: black;
                    opacity: .8;
                    top: 0;
                    left: 0;
                    transition: opacity .45s ease;
                }
            }
            li.is-current:after{
                opacity: 0;
            }
            img{
                display: block;
            }
        }
        .bx-controls{
            a{
                display: inline-block;
                width: 30px;
                height: 30px;
                background-color: $color-black;
                background-size: 9px 16px;
                background-position: center;
                background-repeat: no-repeat;
                text-indent: -99999px;
                position: absolute;
                top: 30%;
            }
            .bx-prev{
                left: -15px;
                background-image: url(../images/icon-arrow-left.png);
            }
            .bx-next{
                right: -15px;
                background-image: url(../images/icon-arrow-right.png);
            }
        }
    }
}


/* pad */
@media(--pad){

.article{
    
    &__content{
        padding-top: 60px;
        max-width: 730px;
        margin: 0 auto;
    }
    .fb-comment{
        max-width: 730px;
        margin: 0 auto;
    }
    &__recommend{
        max-width: 730px;
        margin: 0 auto 30px;
        .title{
            font-size: 22px;
        }
        .bx-controls{
            bottom: 120px;
            a{
                width: 40px;
                height: 40px;
                background-size: 13px 22px;
                top: calc(210/296*100/2)%;
                background-color: rgba(0,0,0,.5);
                margin-top: -10px;
            }
            .bx-next{
                right: 0;
            }
            .bx-prev{
                left: 0;
            }
        }
    }
    &__video{
        max-width: 730px;
        margin: 0 auto 30px;
        .title{
            font-size: 22px;
            text-align: left;
        }
        .play-button{
            width: 65px;
            height: 65px;
            margin: -32.5px 0 0 -32.5px;
            &:before{
                border-top: 12px solid transparent;
                border-bottom: 12px solid transparent;
                border-left: 20px solid white;
                margin: -12px 0 0 -6px;
            }
        }
        li{
            /*position: relative;*/
        }
        .info{
            width: 100%;
            position: absolute;
            bottom: 0;
            background-color: transparent;
            color: white;
            span{
                font-size: 16px;
            }
            .subject{
                font-size: 24px;
            }
            .date{
                font-size: 14px;
            }
        }
        .thumb{
            display: block;
            li{
                cursor: pointer;
            }
        }
        .bx-controls{
            a{
                width: 40px;
                height: 40px;
                background-size: 13px 22px;
                background-color: rgba(0,0,0,.5);
                top: 50%;
                margin-top: -20px;
            }
            .bx-next{
                right: 0;
            }
            .bx-prev{
                left: 0;
            }
        }
    }
}


}

/* desktop */

@media(--desktop){

.article{
    &__slider{
        position: fixed;
        width: calc(920/1900*100)%;
        height: 100%;
        left: 0;
        top: 100px;
        .bx-controls{
            bottom: -30px;
            a{
                width: 30px;
                height: 30px;
                background-size: 9px 16px;
            }
        }
        .slider__thumb{
            bottom: 180px;
            width: 80%;
            .bx-wrapper{
                padding: 5px;
            }
            li{
                cursor: pointer;
            }
        }
    }
    .main-wrapper .container{
        padding-left: calc(920/1900*100)%;
        
    }
    &__content{
        .subject{
            font-size: 32px;
        }
    }
    .post{
        margin-bottom: 35px;
    }
    &__section{
        padding: 0 55px;
        h2{
            font-size: 24px;
            margin: 40px 0;
        }
        p{
            font-size: 16px;
            margin: 0 0 1em 0;
        }
        .title{
            font-size: 24px;
        }
        .tag{
            text-align: center;
            padding-top: 10px;
            a{
                font-size: 14px;
                padding: 0 10px;
            }
        }
    }
    .section__tag{
        .title{
            margin-bottom: 20px;
        }
    }
    
    &__recommend,
    &__video{
        padding: 0 55px;
        .info{
            .date{
                font-size: 12px;
            }
        }
    }
    &__video{
        margin: 0 auto 55px;
    }
    .author{
        margin: 0 55px 55px 55px;
        .cover{
            width: 120px;
        }
        &__intro{
            padding-left: 40px;
            .name{
                font-size: 14px;
            }
            .text{
                font-size: 14px;
            }
        }
    }
    &__recommend{
        .bx-controls{
            a{
                top: 18%;
                margin-top: 0;
            }
        }
    }
    .fb-comment{
        padding: 35px 55px 0 55px;
    }
    .subscribe,
    #footer{
        margin-left: calc(920/1900*100)%;
    }
}

}

/* desktop-w */
@media(--desktop-w){

.article{
    &__slider{
        width: calc(920/1920*100)%;
        .slider__thumb{
            left: 50px;
        }
    }
    .main-wrapper .container{
        padding-left: calc(920/1920*100)%;
    }
    &__recommend,
    &__video{
        .bx-controls{
            opacity: 0;
            transition: opacity .35s ease;
        }
        &:hover{
            .bx-controls{
                opacity: 1;
            }
        }
    }
    &__recommend{
        .bx-controls{
            a{
                top: calc(163/266*100/2)%;
                margin-top: -10px;
            }
        }
    }
    &__video{
        margin: 0 auto 80px;
    }
    .subscribe{
        margin-left: calc(920/1920*100)%;
    }
    #footer{
        margin-left: calc(920/1920*100)%;
    }
}

}