.news__list,
.people__post,
.popular__list,
.article__recommend{
    padding: 0 20px;
    li{
        margin-bottom: 20px;
        letter-spacing: .05em;
    }
    a{

    }
    .title{
        font-size: 18px;
        text-align: center;
        margin-bottom: 10px;
    }
    .cover{
        margin-bottom: 12px;
        img{display: block;width: 100%;}
    }
    .info{
        .type{
            font-family: 'Lato', sans-serif;
            font-size: 13px;
            color: $color-gray-dark;
            text-transform: uppercase;
            margin-bottom: 5px;
        }
        .subject{}
        .date{
            font-family: 'Lato', sans-serif;
            text-transform: uppercase;
            span{
                padding: 0 4px;
                font-size: 14px;
                display: inline-block;
                vertical-align: middle;
                color: $color-gray-dark;
            }
            span:first-child{
                padding: 0;
            }
            span:first-child:after{
                content: "";
                display: inline-block;
                vertical-align: middle;
                margin: 0 5px 3px 10px;
                width: 1px;
                height: 12px;
                background-color: $color-red;
            }
            a{
                color: $color-gray-dark;
            }
        }
    }
    @media(--pad){
        .title{
            font-size: 22px;
            margin-bottom: 20px;
            text-align: left;
        }
        ol{
            display: inline-flex;
            flex-wrap: wrap;
            width: 100%;
        }
        li{
            width: calc((100% - 40px)/3);
            margin-right: 20px;
            &:nth-of-type(3n){
                margin-right: 0;
            }
        }
        .info{
            .subject{
                margin-bottom: 10px;
                color:#fff;
            }
            .date{
                span{
                    font-size: 12px;
                }
            }
        }
    }
    @media(--desktop){
        padding: 0 55px;
        .info{
            .subject{
                // font-size: 18px;
                line-height: 24px;
                letter-spacing: .05em;
                color: #000;
            }
            .date{
                span{
                    font-size: 14px;
                }
            }
        }
    }
    @media(--desktop-w){
        padding: 0 55px;
    }
}

.button__more{
     display: block;
     background-color: $color-black;
     color: white;
     text-align: center;
     line-height: 35px;
     font-size: 14px;
     font-family: 'Lato', sans-serif;
     text-transform: uppercase;
     margin-bottom: 20px;
     letter-spacing: .075em;
        
    @media(--pad){
         width: 270px;
         margin: 40px auto;
    }
    @media(--desktop){
        line-height: 30px;
        font-size: 16px;
        margin: 25px auto 35px;
    }
}
.news__list .info .subject{
    color: #000;
}
@media(--desktop){
    .popular__list li:first-child .info .subject{
        color: #fff;
    }
}