.index{
    &__slider{
        margin-bottom: 20px;
        opacity: 0;
        transition: opacity .25s ease;
        &.is-loaded{
            opacity: 1;
        }
        li{

        }
        .slider__img{
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            z-index: 1;
            a{
                display: block;
                height: 100%;
                background-size: cover;
                background-position: center top;
            }
        }
        .is-current{
            .slider__info{
                transition: transform .35s ease, opacity .45s ease;
                transform: translate(0,0);
                opacity: 1;
            }
        }
        .slider__info{
            position: absolute;
            z-index: 2;
            background-color: white;
            padding: 10px;
            left: 0;
            bottom: 60px;
            opacity: 0;
            transform: translate(20px,0);
            width: calc(100% - 30px);
            letter-spacing: .05em;
            .type{
                font-family: 'Lato', sans-serif;
                font-size: 14px;
                color: $color-gray-dark;
                text-transform: uppercase;
            }
            .date{
                text-transform: uppercase;
                span{
                    padding: 0 4px 0 0;
                    font-size: 14px;
                    display: inline-block;
                    vertical-align: middle;
                }
                span:first-child:after{
                    content: "";
                    display: inline-block;
                    vertical-align: middle;
                    margin-left: 10px;
                    margin-right: 5px;
                    width: 1px;
                    height: 14px;
                    background-color: $color-red;

                }
            }
        }
        .bx-controls{
            position: absolute;
            width: 100%;
            bottom: 20px;
            z-index: 2;
            a{
                display: inline-block;
                width: 40px;
                height: 40px;
                background-size: 14px 25px;
                background-color: $color-black;
                background-position: center;
                background-repeat: no-repeat;
                text-indent: -99999px;
            }
            .bx-prev{
                background-image: url(../images/icon-arrow-left.png);
            }
            .bx-next{
                background-image: url(../images/icon-arrow-right.png);
            }
        }
    }
    .ad-banner{
        margin-bottom: 30px;
    }
    .popular__list{
        display: none;
    }
    &__video{
        .background{
            display: none;
        }
        li{

        }
        .cover{
            position: relative;
            img{
                position: relative;
                z-index: 1;
                display: block;
            }
            .iframe{
                position: absolute;
                padding-bottom: 56.46%;
                height: 0;
                overflow: hidden;
                left: 0;
                top: 0;
                z-index: 3;
                width: 100%;
                iframe{
                    background: #000;
                    position: absolute;
                    top:0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                }
            }
        }
        .info{
            padding: 10px 0;
            text-align: center;
            & > span{
                font-family: 'Lato', sans-serif;

            }
        }
        .play-button{
            width: 40px;
            height: 40px;
            cursor: pointer;
            border: 2px solid white;
            border-radius: 50%;
            position: absolute;
            left: 50%;
            top: 50%;
            margin: -20px 0 0 -20px;
            z-index: 2;
            &:before{
                content: "";
                width: 0; 
                height: 0; 
                border-top: 6px solid transparent;
                border-bottom: 6px solid transparent;
                border-left: 10px solid white;
                position: absolute;
                top: 50%;
                left: 50%;
                margin: -6px 0 0 -3px;
            }
        }
        .thumb{
            ol{
                display: flex;
            }
            li{
                position: relative;
                &:after{
                    content: "";
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    background-color: black;
                    opacity: .7;
                    top: 0;
                    left: 0;
                    transition: opacity .45s ease;
                }
            }
            li.is-current:after{
                opacity: 0;
            }
            img{
                display: block;
            }
        }
        
    }
    
    @media(--pad){
        &__slider{
            .slider__info{
                left: 20px;
                width: 65%;
                /*padding: 20px;*/
                bottom: 100px;
                .type{
                    font-size: 16px;
                }
                .subject{
                    min-height: 2.5em;
                    font-size: 24px;
                    margin-bottom: 10px;
                }
                .date{

                }
            }
            .bx-controls{
                bottom: 70px;
                width: auto;
                left: 20px;
                a{
                    width: 30px;
                    height: 30px;
                    background-size: 9px 16px;
                }
            }
        }
        .ad-banner-first{
            margin-bottom: 40px;
        }
        .popular__list{
            display: block;
            padding-top: 20px;
            padding-bottom: 30px;
            margin-bottom: 30px;
            // margin-top: -30px;
            position: relative;
            background-color: #ededed;
            .backgroundImage{
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                opacity: .08;
                z-index: 0;
            }
            ol{
                position: relative;
                z-index: 1;
            }
            .title{
                text-align: center;
            }
            li{
                position: relative;
            }
            li:nth-child(1),
            li:nth-child(2){
                width: calc((100% - 20px)/2);
            }
            li:nth-child(2){
                margin-right: 0;
            }
            li:nth-child(3){
                margin-right: 20px;
            }
            li:nth-child(5){
                margin-right: 0;
            }
            .cover{
                margin-bottom: 0;
                img{
                    width: 100%;
                }
            }
            .info{
                position: absolute;
                width: 100%;
                left: 0;
                bottom: 0;
                background-color: rgba(0,0,0,.7);
                padding: 10px 15px;
                .type{
                    margin-bottom: 5px;
                }
                .subject{
                    color: white;
                    margin-bottom: 0;
                }
            }
        }
        &__video{
            position: relative;
            padding: 40px;
            &:before{
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                background-color: rgba(0,0,0);
                // background-color: #ebebeb;
                left: 0;
                top: 0;
                z-index: 1;
            }
            .background{
                display: block;
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                background-size: cover;
                background-position: center;
                z-index: 2;
                opacity: .3;
            }
            .list,.thumb{
                position: relative;
                z-index: 3;
            }
            li{
                position: relative;
            }
            .cover{
                img{
                    width: 100%;
                }
            }
            .info{
                position: absolute;
                width: 100%;
                bottom: 20px;
                left: 0;
                color: white;
                & > span{
                    
                }
                .subject{
                    font-size: 24px;
                }
                .date{
                    font-size: 14px;
                }
            }
            .play-button{
                width: 65px;
                height: 65px;
                margin: -32.5px 0 0 -32.5px;
                &:before{
                    border-top: 12px solid transparent;
                    border-bottom: 12px solid transparent;
                    border-left: 20px solid white;
                    margin: -12px 0 0 -6px;
                }
            }
            .thumb{
                padding: 10px 0 0 0;
                ol{
                    justify-content: center;
                }
                li{
                    margin-right: 10px;
                    cursor: pointer;
                }
                li:last-child{
                    margin-right: 0;
                }
            }
        }
    }

    @media(--desktop){
        &__slider{
            position: fixed;
            width: calc(920/1900*100)%;
            height: 100%;
            left: 0;
            top: 100px;
            .slider__info{
                left: 30px;
                width: calc(540/920*100)%;
            }
            .bx-controls{
                left: 30px;
            }
        }
        .popular__list{
            margin-bottom: 45px;
        }
        .main-wrapper .container{
            padding-left: calc(920/1900*100)%;
        }
        .subscribe{
            margin-left: calc(920/1900*100)%;
        }
        #footer{
            margin-left: calc(920/1900*100)%;
        }
    }

    @media(--desktop-w){
        &__slider{
            // width: calc(920/1900*100)%;
            .slider__info,
            .bx-controls{
                left: 55px;
            }
        }
        .main-wrapper .container{
            // padding-left: calc(920/1900*100)%;
        }
        .subscribe{
            // margin-left: calc(920/1900*100)%;
        }
        #footer{
            // margin-left: calc(920/1900*100)%;
        }
    }

    @media(--desktop-hd){
        &__slider{
            .slider__info{
                padding: 20px 30px;
            }
        }
    }
}

